.ContactForm {
    border: 1px solid #1e90ff;
    border-radius: 5px;
    margin: 20px 0 20px 0;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
    background-color: #d0d0d0;
}

.FormHeading {
    font-size: 16px;
    padding: 8px 8px;
    font-weight: 700;
    color: #1e90ff;
}

.ButtonContainer {
    min-height: 50px;
}

/* Styles for screen sizes larger than 992px wide */
@media (min-width: 992px) {
    .FormHeading {
        font-size: 28px;
    }
}